.lecture {

  &__img {
    height: 550px;

    background-image: url($img-lecture);
    background-size: cover;
    background-position: bottom;
  }

  &__info {
    position: relative;
    bottom: 102px;

    margin: 0 auto;
    padding: 45px 100px 50px;

    width: 570px;
    height: 204px;

    background-color: #fff;
    box-shadow: 0 2px 0 rgba(26, 90, 76, 0.25);
  }

  &__details {
    display: flex;
    justify-content: space-between;

    margin-bottom: 23px;
  }
}
